import * as THREE from "three";
import Stats from "three/examples/jsm/libs/stats.module.js";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";

export default class GolfScene {
    constructor(container) {
        this.container = container;
        this.init();
    }

    init() {
        this.addRenderer();
        this.addScene();
        this.addModel();
        this.addLight();
        this.addCamera();
        this.addOrbitControl();
        this.addStats();
    }

    addScene() {
        const scene = new THREE.Scene();
        scene.background = new THREE.Color(0xffffff);
        this.scene = scene;
    }

    addCamera() {
        const camera = new THREE.PerspectiveCamera(90, window.innerWidth / window.innerHeight, 1, 10000);
        camera.position.z = 100;
        this.camera = camera;
    }

    addRenderer() {
        const renderer = new THREE.WebGLRenderer();
        renderer.setPixelRatio(window.devicePixelRatio);
        renderer.setSize(window.innerWidth, window.innerHeight);
        renderer.toneMapping = THREE.ACESFilmicToneMapping;
        renderer.toneMappingExposure = 1.25;
        renderer.outputEncoding = THREE.sRGBEncoding;

        this.container.appendChild(renderer.domElement);
        this.renderer = renderer;
    }

    addModel() {
        const textureLoader = new THREE.TextureLoader();
        const normalMap = textureLoader.load("assets/golfball/normal.jpg");
        const geometry = new THREE.SphereBufferGeometry(50, 64, 64);
        const material = new THREE.MeshPhysicalMaterial({
            metalness: 0.0,
            roughness: 0.1,
            normalMap: normalMap,
        });
        const mesh = new THREE.Mesh(geometry, material);
        mesh.position.x = 0;
        mesh.position.y = 0;
        this.scene.add(mesh);
    }

    addOrbitControl() {
        new OrbitControls(this.camera, this.renderer.domElement);
    }

    addLight() {
        const particleLight = new THREE.Mesh(new THREE.SphereBufferGeometry(4, 8, 8), new THREE.MeshBasicMaterial({ color: 0xffffff }));
        particleLight.position.z = 110;
        this.scene.add(particleLight);
        particleLight.add(new THREE.PointLight(0xffffff, 1.5, 200));
        this.particleLight = particleLight;
    }

    addStats() {
        const stats = new Stats();
        this.container.appendChild(stats.dom);
        this.stats = stats;
    }

    render = () => {
        const { particleLight, renderer, scene, camera, render } = this;
        const timer = Date.now() / 1000;
        particleLight.position.x = Math.sin(timer * 4) * 100;
        particleLight.position.y = Math.sin(timer * 2) * 100;

        renderer.render(scene, camera);
        this.stats.update();
        requestAnimationFrame(render);
    };
}
