import React, { useEffect, useRef } from "react";
import GolfScene from "../Scene/Golf/GolfScene";

export default function Golf() {
    const WebGLRef = useRef();

    useEffect(() => {
        const scene = new GolfScene(WebGLRef.current);
        scene.render();
    }, []);

    return <div id='CPR' ref={WebGLRef}></div>;
}
